import './upfooter.css';
import {ChakraProvider,Button,Tag} from "@chakra-ui/react";
import { DragHandleIcon,ChevronRightIcon,EmailIcon } from '@chakra-ui/icons';
import ScrollAnimation from 'react-animate-on-scroll';
import "animate.css/animate.min.css";

function Upfooter() {
    return(
        <div>
            <ScrollAnimation animateIn="animate__bounceInLeft" offset="300" duration={1}>
            <div class="upper">
                <strong class="upper-strong">Design news</strong><br />
                <span class="upper-span">Best place for lasted Design news</span>
                <ChakraProvider>
                <Button className={"upper-button"} leftIcon={<ChevronRightIcon />}  variant="solid">check out</Button><br/>
                <div class="upper-tags">
                <Tag className={"upper-tag"}>New models</Tag>
                <Tag className={"upper-tag"}>styles</Tag>
                <Tag className={"upper-tag"}>marketing</Tag>
                </div>
                </ChakraProvider>
            </div>
            </ScrollAnimation>
        </div>
    )
}
export default Upfooter;