import './navbar.css';
import SearchField from "react-search-field";
import { FaReact } from "react-icons/fa";
import { GrTwitter } from "react-icons/gr";
import { FaFacebook } from "react-icons/fa";
import { RiInstagramFill } from "react-icons/ri";
import { SiYoutube } from "react-icons/si";
import { FaReddit } from "react-icons/fa";
import ScrollAnimation from 'react-animate-on-scroll';
import "animate.css/animate.min.css";
//sign in
import {  useState } from "react";
import '../other/sign/sign.css';
import { Button,ChakraProvider } from "@chakra-ui/react";
import { FiSend } from 'react-icons/fi';

function Navbar(){
    const [sign, setSign] = useState(false);
    return(
        <div>
            <ScrollAnimation animateIn="animate__fadeInDown" offset="0" duration={3}>
            <header class="header">
                <div class="logos2">
                <GrTwitter class="logo2"/>
                <FaFacebook class="logo2"/>
                <RiInstagramFill class="logo2"/>
                <SiYoutube class="logo2"/>
                <FaReddit class="logo2"/>
                </div>
                <p class="logo2txt">follow us in social media</p>
            </header>
            </ScrollAnimation>
        <div class="navbar">
            <div class="put">   
            <SearchField placeholder="Search..."  onChange={""} classNames="searchnav"/>
            <div className="line" ></div>
            </div>
            <div class="links">
            <a href="#">Home</a>
            <a href="#">Services</a>
            <a href="#">contact us</a>
            <a href="#" onClick={() => setSign(!sign)}>Sign in</a>
            </div> 
            <FaReact src="logo512.png" class="logo"/>
            <span class="logotxt"><strong class="strong">R</strong>eact Js</span>
        </div>




        
        {
        sign &&
                        <div style={{position:"fixed" , height:"100%" , width:"100%" , background:"rgba(0,0 ,0 ,0.5)", top:"0" , left:"0"}} class="in"  onClick={() => setSign(!sign)} >
          <center>
            <div class="form"  onClick={(e) => e.stopPropagation()}>
              
              <input type="text" placeholder="Name" class="sign-input"/>
              <div class="lineform"></div>
              <input type="text" placeholder="Email" class="sign-input"/>
              <div class="lineform"></div>
              <input type="password" placeholder="Password" class="sign-input"/>
              <div class="lineform"></div>
              <ChakraProvider>
              <Button leftIcon={<FiSend />} className={"sign-button"}>Send</Button>
              </ChakraProvider>
            </div>
          </center>
        </div>
        }
        </div>
    )
}
export default Navbar;