import react from "react";
import './style.css';
import ScrollAnimation from 'react-animate-on-scroll';
import "animate.css/animate.min.css";
import Navbar from './navbar/navbar';
import Slider from './slider/slider';
import Content from './content/content';
import Content2 from './content2/content2';
import Map from './map/map';
import './content/glow.css';
import Upfooter from "./upfooter/upfooter";
import Footer from "./footer/footer";

import './marquee/marquee.css';
import styled, { createGlobalStyle } from "styled-components";
import {useWindowSize,useWindowWidth,useWindowHeight,} from '@react-hook/window-size';
import {AiFillHome ,AiFillMedicineBox , AiOutlineDesktop } from "react-icons/ai";
import { FiSearch } from "react-icons/fi";
import { BsInfoCircleFill, BsFillCapslockFill  } from "react-icons/bs";
import { FiPlusCircle } from "react-icons/fi";

//sign in
import {  useState } from "react";
import './other/sign/sign.css';
import { Button,ChakraProvider } from "@chakra-ui/react";
import { FiSend } from 'react-icons/fi';

function App() {
  const [widthOFwindow, heightOFwindow] = useWindowSize({ fps: 60 });
  const [sign, setSign] = useState(false);

  return(
    <div>
      <ScrollAnimation animateIn="animate__fadeInDown" offset="0" duration={1}>
      <Navbar />
      </ScrollAnimation>
      <Slider />
      <div class="text">
      <center>
      <div class="light"></div>
      </center>  
      <span>4K" refers to horizontal resolutions of around 4,000 pixels. The "K" stands for "kilo" (thousand). As things stand, the majority of 4K displays come with 3840 x 2160 pixel</span>
      </div>
      <ScrollAnimation animateIn="animate__backInLeft" offset="400" duration={1}>
      <div class="marquee">
  <div class="marquee__inner">
    <h1>
    4K" refers to horizontal resolutions of around 4,000 pixels. The "K" stands for "kilo" (thousand). As things stand, the majority of 4K displays come with 3840 x 2160 pixel
    </h1>
  </div>
</div>
      </ScrollAnimation>
      <Content />
      <Content2 />
      <Map />
      <Upfooter />
      <Footer />

      { widthOFwindow < 768 &&
      
    <div style={{width:"100vw" , height:"10vh" , position:"fixed" , left:"0" ,bottom:"0" }} class="tab-bar"  >
      <div class="glow"></div>
      <BottomNavigationV class="tab-bar2">

      <NavItem className={"home"} ><AiFillHome style={{ fontSize: '6vh' }} /></NavItem>
      <NavItem className={"home"} ><FiSearch style={{ fontSize: '6vh' }} /></NavItem>
      <NavItem className={"home"}  ><BsInfoCircleFill style={{ fontSize: '6vh' }} /></NavItem>
      <NavItem className={"home"}  onClick={() => setSign(!sign)} ><FiPlusCircle style={{ fontSize: '6vh' }} /></NavItem>


      </BottomNavigationV>
    </div>
}


{
        sign &&
                        <div style={{position:"fixed" , height:"100%" , width:"100%" , background:"rgba(0,0 ,0 ,0.5)", top:"0" , left:"0"}} class="in"  onClick={() => setSign(!sign)} >
          <center>
            <div class="form"  onClick={(e) => e.stopPropagation()}>
              
              <input type="text" placeholder="Name" class="sign-input"/>
              <div class="lineform"></div>
              <input type="text" placeholder="Email" class="sign-input"/>
              <div class="lineform"></div>
              <input type="password" placeholder="Password" class="sign-input"/>
              <div class="lineform"></div>
              <ChakraProvider>
              <Button leftIcon={<FiSend />} className={"sign-button"}>Send</Button>
              </ChakraProvider>
            </div>
          </center>
        </div>
        }
    </div>
  )
}
const BottomNavigationV = styled.div`
position: relative;
width: 100%;
display: flex;
justify-content: space-between;
bottom: 0;
right: 0;
background-color: 0;
z-index: 100;
height: 100%;

backdrop-filter: blur(5px);
`;


const NavItem = styled.div`
flex-grow: 1;
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
height: 100%;
transition: all 0.3s ease;
width:100%;
z-index: 4;
`;

const NavText = styled.p`
margin-top: 3px;
font-size: 1vh;
margin-bottom: 5px;
fontWeight:"700"
`;
export default App;