import './content2.css';
import ScrollAnimation from 'react-animate-on-scroll';
import "animate.css/animate.min.css";
import { Grid, GridItem, ChakraProvider, Button, ButtonGroup,CircularProgress, CircularProgressLabel } from "@chakra-ui/react";
import { ViewIcon,ExternalLinkIcon,PlusSquareIcon } from '@chakra-ui/icons';
import { Icon } from "@chakra-ui/react"
import { FaShoppingCart } from "react-icons/fa"

function Content2() {
    return(
        <div>
            <ChakraProvider>
            <Grid
             w="100%"
             templateRows={{base:"repeat(72, 1rem)",sm:"repeat(67, 1rem)",md:"repeat(35, 1rem)",lg:"repeat(35, 1rem)",xl:"repeat(35, 1rem)"}}
             templateColumns={{base:"repeat(50, 1fr)", sm:"repeat(35, 1fr)",md:"repeat(30, 1fr)",lg:"repeat(30, 1fr)",xl:"repeat(30, 1fr)"}}
             gap={0}
            >

                <GridItem className={"f1"} colStart={{base:2 ,sm:2, md:2,lg:2 ,xl:2}} colEnd={{base:50, sm:35, md:30,lg:30 ,xl:30}} rowStart={{base:3 ,sm:4, md:5,lg:5 ,xl:5}} rowEnd={{base:69 ,sm:65, md:32,lg:32 ,xl:32}} ></GridItem>

                <GridItem className={"f2"} colStart={{base:2 ,sm:6, md:2,lg:2 ,xl:2}} colEnd={{base:50, sm:31, md:15,lg:15 ,xl:14}} rowStart={{base:3 ,sm:4, md:5,lg:5 ,xl:5}} rowEnd={{base:18 ,sm:18, md:17,lg:17 ,xl:17}} >
                    <div class="f-co">
                        <div class="f-img">
                            <img class="f-image" src="39158911.jpg"/>
                        </div>
                        <ScrollAnimation animateIn="animate__fadeIn" offset="400" duration={2}>
                        <div class="f-frame">
                            <p class="f-title">Our company</p>
                            <p class="f-txt">contact us for get more informations about of all contracts</p>
                            <center>
                            <Button colorScheme="teal" size="md" className={"f-button"} leftIcon={<ViewIcon />}>See more</Button>
                            </center>
                        </div>
                        </ScrollAnimation>
                    </div>
                </GridItem>


                <GridItem className={"f3"} colStart={{base:2 ,sm:6, md:17,lg:17 ,xl:18}} colEnd={{base:50, sm:31, md:30,lg:30 ,xl:30}} rowStart={{base:20 ,sm:19, md:5,lg:5 ,xl:5}} rowEnd={{base:35 ,sm:33, md:17,lg:17 ,xl:17}} >
                <div class="f-co">
                        <div class="f-img">
                            <img class="f-image" src="13444831.jpg"/>
                        </div>
                        <ScrollAnimation animateIn="animate__fadeIn" offset="400" duration={2}>
                        <div class="f-frame">
                            <p class="f-title">Interior design</p>
                            <p class="f-txt">Interior design and customize your propertys</p>
                            <center>
                            <Button colorScheme="teal" size="md" className={"f-button"} leftIcon={<ExternalLinkIcon />}>Edition</Button>
                            </center>
                        </div>
                        </ScrollAnimation>
                    </div>
                </GridItem>


                <GridItem className={"f4"} colStart={{base:2 ,sm:6, md:2,lg:2 ,xl:2}} colEnd={{base:50, sm:31, md:15,lg:15 ,xl:14}} rowStart={{base:37 ,sm:35, md:20,lg:20 ,xl:20}} rowEnd={{base:52 ,sm:49, md:32,lg:32 ,xl:32}} >
                <div class="f-co">
                        <div class="f-img">
                            <img class="f-image" src="construction-1-1478228-639x4261.jpg"/>
                        </div>
                        <ScrollAnimation animateIn="animate__fadeIn" offset="400" duration={2}>
                        <div class="f-frame">
                            <p class="f-title">New projects</p>
                            <p class="f-txt">We constraction your projects</p>
                            <center>
                            <Button colorScheme="teal" size="md" className={"f-button"} leftIcon={<PlusSquareIcon />}>Create</Button>
                            </center>
                        </div>
                        </ScrollAnimation>
                    </div>
                </GridItem>


                <GridItem className={"f5"} colStart={{base:2 ,sm:6, md:17,lg:17 ,xl:18}} colEnd={{base:50, sm:31, md:30,lg:30 ,xl:30}} rowStart={{base:54 ,sm:51, md:20,lg:20 ,xl:20}} rowEnd={{base:69 ,sm:65, md:32,lg:32 ,xl:32}} >
                <div class="f-co">
                        <div class="f-img">
                            <img class="f-image" src="33541.jpg"/>
                        </div>
                        <ScrollAnimation animateIn="animate__fadeIn" offset="400" duration={2}>
                        <div class="f-frame">
                            <p class="f-title">buy a property</p>
                            <p class="f-txt">buy a super project or apartment</p>
                            <center>
                            <Button colorScheme="teal" size="md" className={"f-button"} leftIcon={<FaShoppingCart />}>Shopping</Button>
                            </center>
                        </div>
                        </ScrollAnimation>
                    </div>
                </GridItem>

            </Grid>
            </ChakraProvider>

        </div>
    )
}
export default Content2;