import './map.css';
import { ChakraProvider, CircularProgress, CircularProgressLabel } from "@chakra-ui/react";
import {
    Stat,
    StatLabel,
    StatNumber,
    StatHelpText,
    StatArrow,
    StatGroup,
  } from "@chakra-ui/react";
  import ScrollAnimation from 'react-animate-on-scroll';
import "animate.css/animate.min.css";

function Map() {
    return(
        <div>
          <ScrollAnimation animateIn="animate__bounceInUp" offset="500" duration={2}>
            <ChakraProvider>
            <center>
                <div class="map">
                <img src="World.svg"/>
                </div>
            </center>
            <div class="cir">
            <CircularProgress value={70} size="2rem" color="blue.400">
            <CircularProgressLabel>70%</CircularProgressLabel>
            </CircularProgress>
            <span style={{paddingLeft:"0.5rem"}}>profit</span>
            </div>

            <div class="rent">
            <StatGroup>
  <Stat>
    <StatLabel>Market</StatLabel>
    <StatNumber>345,670</StatNumber>
    <StatHelpText>
      <StatArrow type="increase" color="#0ff" />
      23.36%
    </StatHelpText>
    <CircularProgress isIndeterminate color="blue.300" size="1rem"/>
  </Stat>
  </StatGroup>
  </div>
            </ChakraProvider>
            </ScrollAnimation>
        </div>
    )
}
export default Map;