import './content.css';
import { Grid, GridItem, ChakraProvider } from "@chakra-ui/react";
import { createBreakpoints } from "@chakra-ui/theme-tools";
import ScrollAnimation from 'react-animate-on-scroll';
import "animate.css/animate.min.css";
import { AspectRatio, Image } from "@chakra-ui/react";


function Content() {
  const breakpoints = createBreakpoints({
    sm: "320px",
    md: "768px",
    lg: "968px",
    xl: "1200px",
  })

    return(
        <div>
          <ChakraProvider>
          <Grid
           w="100%"
           templateRows={{base:"repeat(50, 1rem)",sm:"repeat(35, 1fr)",md:"repeat(15, 1fr)",lg:"repeat(15, 1fr)",xl:"repeat(15, 1fr)"}}
           templateColumns={{base:"repeat(50, 1fr)", sm:"repeat(35, 1fr)",md:"repeat(30, 1fr)",lg:"repeat(30, 1fr)",xl:"repeat(30, 1fr)"}}
           gap={0}
          >
          <GridItem className={"stage1"} colStart={{base:2, sm:1, md:1,lg:2 ,xl:2}} colEnd={{base:50, sm:36, md:31,lg:30 ,xl:30}} colSpan={{base:51, sm:36, md:31,lg:31 ,xl:31}}  rowSpan={{base:80, sm:40, md:20 ,lg:19 ,xl:15}} rowStart={{base:2, sm:1, md:1,lg:1 ,xl:1}}  >
          <ScrollAnimation animateIn="animate__fadeIn" offset="0" duration={5}>
          <img src="svg2.png" class="svg" />
          </ScrollAnimation>  
          </GridItem>

          <GridItem className={"c1"} colStart={{base:6, sm:2, md:2,lg:4 ,xl:3}} colEnd={{base:46, sm:18, md:11,lg:12 ,xl:11}} rowSpan={{base:20, sm:15, md:10,lg:9 ,xl:9}} rowStart={{base:12, sm:2, md:4,lg:4 ,xl:4}} >
          <div class="fade">
          <div class="content">
          <div class="content-overlay"></div>
          <img src="a93f68dcd3c1e7e1072628a240acb553.webp" />
          <div class="content-details fadeIn-bottom">
          <h3 class="content-title">comming soon</h3>
          <p class="content-text">under constractions</p>
          </div>
          </div>
          </div>
          <ScrollAnimation animateIn="animate__fadeIn" offset="400" duration={2}>
          <p class="c-txt">Construction of the tower began in 2021</p>
          </ScrollAnimation>
          </GridItem>

          <GridItem className={"c2"} colStart={{base:6, sm:20, md:11,lg:12 ,xl:11}} colEnd={{base:46, sm:35, md:21,lg:21 ,xl:21}} rowSpan={{base:20, sm:15, md:12,lg:12 ,xl:12}} rowStart={{base:34, sm:2, md:3,lg:3 ,xl:3}} >
          <div class="fade">
          <div class="content">
          <div class="content-overlay"></div>
          <img src="3373581.webp" />
          <div class="content-details fadeIn-bottom">
          <h3 class="content-title">Servises</h3>
          <p class="content-text">conditions and contracts</p>
          </div>
          </div>
          </div>
          <ScrollAnimation animateIn="animate__fadeIn" offset="400" duration={2}>
          <p class="c-txt" style={{marginTop:"1.3rem"}}>Services of a professional property management company</p>
          </ScrollAnimation>
          

         
          </GridItem>

          <GridItem className={"c3"} colStart={{base:6, sm:11, md:21,lg:21 ,xl:21}} colEnd={{base:46, sm:27, md:30,lg:29 ,xl:29}} rowSpan={{base:20, sm:16, md:10,lg:9 ,xl:9}} rowStart={{base:56, sm:19, md:4,lg:4 ,xl:4}} >
          <div class="fade">
          <div class="content">
          <div class="content-overlay"></div>
          <img src="1218437.webp" />
          <div class="content-details fadeIn-bottom">
          <h3 class="content-title">propertys</h3>
          <p class="content-text">All of locations</p>
          </div>
          </div>
          </div>
          <ScrollAnimation animateIn="animate__fadeIn" offset="400" duration={2}>
          <p class="c-txt">Find landlord management company</p>
          </ScrollAnimation>
          </GridItem>

          </Grid>
          </ChakraProvider>
    </div>
        
    )
}
export default Content;