import './footer.css';
import { Button } from "@chakra-ui/react";
import { EmailIcon } from '@chakra-ui/icons';
import { GrTwitter } from "react-icons/gr";
import { FaFacebook } from "react-icons/fa";
import { RiInstagramFill } from "react-icons/ri";
import { SiYoutube } from "react-icons/si";
import { FaReddit } from "react-icons/fa";
import {ChevronDownIcon} from '@chakra-ui/icons';
import {
    Menu,
    MenuButton,
    MenuList,
    MenuItem,
    Progress,
    ChakraProvider
  } from "@chakra-ui/react";
  import ScrollAnimation from 'react-animate-on-scroll';
  import "animate.css/animate.min.css";

function Footer(){
    return(
        <ScrollAnimation animateIn="animate__bounceInUp" offset="450" duration={1}>
        <div class="fild">
            
  
  <div class="fild1">
  <center>
      <p>Follow us in social media</p> 
      </center>
      <Button leftIcon={<EmailIcon />} colorScheme="teal" variant="solid"  class="footer-a">Email</Button><br />
      <Button leftIcon={<GrTwitter />} colorScheme="teal" variant="solid"  class="footer-a">Twitter</Button><br />
      <Button leftIcon={<FaFacebook />} colorScheme="teal" variant="solid"  class="footer-a">Facebook</Button><br />
      <Button leftIcon={<RiInstagramFill />} colorScheme="teal" variant="solid"  class="footer-a">Instagram</Button><br />
      <Button leftIcon={<SiYoutube />} colorScheme="teal" variant="solid"  class="footer-a">Youtube</Button><br />
      <Button leftIcon={<FaReddit />} colorScheme="teal" variant="solid"  class="footer-a">Reddit</Button><br />

  </div>
 

  <div class="fild2">
      <a href="#" class="footer-a">Services</a><br />
      <a href="#" class="footer-a">Contact us</a><br />
      <a href="#" class="footer-a">Contracts</a><br />
      <a href="#" class="footer-a">Projects</a><br />
      <a href="#" class="footer-a">Shopping</a><br />
      <a href="#" class="footer-a">News</a><br />
      <a href="#" class="footer-a">Market cap</a><br />
  </div>  
  <div class="fild3">
  <Menu>
  <MenuButton as={Button} className={"menu"} rightIcon={<ChevronDownIcon />}>
    Actions
  </MenuButton>
  <MenuList className={"list"}>
    <MenuItem>Download</MenuItem>
    <MenuItem>Create a Copy</MenuItem>
    <MenuItem>Mark as Draft</MenuItem>
    <MenuItem>Delete</MenuItem>
    <MenuItem>Attend a Workshop</MenuItem>
  </MenuList>
</Menu>
<ChakraProvider>
<Progress value={80} size="sm" colorScheme="pink" bg="snow" className={"progress"} />
<span>Clients %80</span>
<Progress value={23} size="sm" colorScheme="pink" bg="snow" className={"progress"} />
<span>Market %23</span>
<Progress size="sm" colorScheme="pink" isIndeterminate className={"progress"}/>
<span>future</span>
</ChakraProvider>
  </div>
  </div>
  </ScrollAnimation>
    )
}
export default Footer;